import React from 'react';
import Logo from '../components/logo/logo';
// eslint-disable-next-line
import {List, InputItem, WingBlank, WhiteSpace, Button, Radio} from 'antd-mobile';
import {connect} from 'react-redux';
import {login} from '../redux/user.redux';
import {Redirect} from 'react-router-dom';
import {getRedirectPath} from '../util';
import form from '../components/form/form';

// import './style.css'
@connect(
  state => (state.user),
  {login}
)
@form
class Login extends React.Component {
  constructor (props) {
    super(props);
    this.handleLogin = this.handleLogin.bind(this);
  }

  register () {
    this.props.history.push('/login');
  };

  // handleChange (key, val) {
  //   this.setState(
  //     {
  //       [key]: val
  //     }
  //   );
  // }

  handleLogin () {
    this.props.login(this.props.state);
  };

  /**
   *
   * @returns {*}
   */
  render () {
    console.log(this.props.redirectTo);
    return (
      <div>
        {this.props.redirectTo&& this.props.redirectTo !== '/login' ? <Redirect to={getRedirectPath(this.props.redirectTo)}/> : null}
        <Logo/>
        <h2></h2>
        {this.props.msg ? <p className='message'>{this.props.msg}</p> : null}
        <WingBlank>
          <List>
            <InputItem placeholder="" onChange={(v) => this.props.handleChange('user', v)}></InputItem>
            <WhiteSpace/>
            <InputItem placeholder="" onChange={v => this.props.handleChange('pwd', v)}></InputItem>
          </List>
          <WhiteSpace/>
          <Button type="primary" onClick={this.handleLogin}></Button>
        </WingBlank>
      </div>
    );
  }
}

export default Login;


